import DeskTopDevCard from "./desktop/DeskTopDevCard"
import MobileDevCard from "./mobile/MobileDevCard";
import { Names } from "./DevData"
import { BrowserView, MobileView} from 'react-device-detect';



function Developer (){

    return(
        <div>

            

        <BrowserView>
            {Names.map((name)=>{
                return(
                    <DeskTopDevCard name={name}/>
                )
            })}
        </BrowserView>

        <MobileView>
        {Names.map((name)=>{
                return(
                    <MobileDevCard name={name}/>
                )
            })}
        </MobileView>
            
        </div>
        

        

        

    )
}

export default Developer