import React from "react";
import { AnimateSharedLayout, AnimatePresence } from "framer-motion";
import { Item } from "./Component/About/Item";
import { List } from "./Component/About/List";
import { Routes, Route, useParams } from "react-router-dom";
import Header from "./Component/About/Header";
import { motion } from "framer-motion";
import Developer from "./Component/Developers/Developers";
import Test from "./Component/Test";
import Desktopmain from "./Component/mmain/Desktop/Desktopmain";
import Mobilemain from "./Component/mmain/mobile/Mobilemain";
import Main from "./Component/mmain/main";
import Experience from "./Component/Experience/Experience";


function Store() {
  let { id } = useParams();
  const imageHasLoaded = true;

  return (
    <motion.div
    initial={{ opacity:0}}
              animate={{ 
                opacity:1,
                duration: 1}}>
      <List selectedId={id} />
      <AnimatePresence
      >
        {id && imageHasLoaded && <Item id={id} key="item" />}
      </AnimatePresence>
    </motion.div>
  );
}

export default function App() {




  return (
    <div className="container">
      <AnimateSharedLayout type="crossfade">
        <Header />     
        <Routes>
          {
            
          }
          <Route path='/' element={<Main></Main>}></Route>
          <Route path='/Bemain/mobile' element={<Mobilemain></Mobilemain>}></Route>
          <Route path='/Bemain/desktop' element={<Desktopmain></Desktopmain>}></Route>
          <Route path='/About/:id' element={<Store></Store>} />
          <Route path='/About' element={<Store></Store>} />
          <Route path='/Experience' element={<Experience/>} />
          <Route path='/Test' element={<Test></Test>} />
          <Route path='/Developers' element={<Developer/>} />
        </Routes>
      </AnimateSharedLayout>
    </div>
  );
}
