import { motion, useAnimation } from "framer-motion"
import { useEffect } from "react"
import { useInView } from "react-intersection-observer"


function MobileMiddle3 (){

    const control = useAnimation()
    const [ref,inView] = useInView()

    const textVariant = {
        visible: {rotate:[0,-10] },
        hidden: {},
      }

    const carVariant = {
        visible: {x:[50,-5,10] },
        hidden: {},
      }

    useEffect(()=>{
        if (inView){
            control.start('visible');
        }else{
            control.start('hidden')
        }
    },[control,inView])

    return(

        <div style={{color:'white',fontSize:'10vw',textAlign:'center',fontWeight:'800'}}>

            <div >
                <div>
                300만건의

                </div>
                <div style={{display:'flex',justifyContent:'center'}}>
                    <div>
                    행정력 &nbsp;
                    </div>
                    <motion.div
                    ref={ref}
                    variants={textVariant}
                    transition={{duration:0.45,times:[0.8,1]}}
                    initial='hidden'
                    animate={control}
                    >
                    사고율

                    </motion.div>
                    <motion.div
                    ref={ref}
                    variants={carVariant}
                    initial='hidden'
                    animate={control}
                    >
                        🚙

                    </motion.div>
                      
                </div>
                <div>
                    줄일수는 없을까요?
                </div>

            </div>

            
            
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            
        </div>

    )
}

export default MobileMiddle3