import { motion, useAnimation } from "framer-motion"
import { useEffect } from "react"
import { useInView } from "react-intersection-observer"


function DesktopMiddle3 (){

    const control = useAnimation()
    const [ref,inView] = useInView()

    const textVariant = {
        visible: {rotate:[0,-10] },
        hidden: {},
      }

    const carVariant = {
        visible: {x:[200,-10,20] },
        hidden: {},
      }

    useEffect(()=>{
        if (inView){
            control.start('visible');
        }else{
            control.start('hidden')
        }
    },[control,inView])



    return(

        <div style={{color:'white',fontSize:'4vw',textAlign:'center',fontWeight:'800'}}>

            

            <div style={{display:'flex',justifyContent:'center'}}>
                <div>
                    300만건의 행정력과 &nbsp;
                </div>
                <motion.div 
                ref={ref}
                variants={textVariant}
                transition={{duration:0.45,times:[0.8,1]}}
                initial='hidden'
                animate={control}
                >
                    사고율
                </motion.div>
                <motion.div
                ref={ref}
                variants={carVariant}
                initial='hidden'
                animate={control}>
                    🚙
                </motion.div>
            </div>

            <div>
                    줄일수는 없을까요?
            </div>

            
            
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            
        </div>

    )
}

export default DesktopMiddle3