import { motion, useAnimation, useTransform, useViewportScroll } from "framer-motion"
import { useEffect } from "react";
import { useInView } from "react-intersection-observer";

function DesktopMiddle1(){

    // const { scrollYProgress } = useViewportScroll();
    // const yPosAnim = useTransform(scrollYProgress, [0, 0.4], [-300, -1000])
    const control = useAnimation()
    const [ref,inView] = useInView()

    const hiddenboxVariant = {
        visible: { opacity:1,fontSize:'5vw'},
        hidden: { opacity:0.5,fontSize:'3vw'},
      }

    useEffect(()=>{
        if (inView){
            control.start('visible');
        }else{
            control.start('hidden')
        }
    },[control,inView])

    return(
        <div style={{color:'white',fontSize:'4vw',fontWeight:'800'}}>
            

            <div
            style={{textAlign:'center',margin:'30px'}}
            >
                <div>최근 4년 교통법규 </div>
                <div>위반 공익신고는</div>
                <div>
                    <motion.span 
                    style={{color:'#FF008C'}}
                    ref = {ref}
                    variants={hiddenboxVariant}
                    transition={{duration:1}}
                    initial="hidden"
                    animate={control}
                    >&nbsp;3배&nbsp;</motion.span>
                    증가하였습니다.</div>
            </div>

            <div style={{opacity:'0.3',fontSize:'1.2vw',textAlign:'center',top:'10%',left:'90%'}}>
                <div>&nbsp;</div>
                <div>104만건 (2018) &nbsp; → &nbsp;290만건 (2021)</div>
                <div>경찰청 제공</div>

            </div>
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            <div style={{fontSize:'100px'}}>&nbsp;</div>
            <div style={{fontSize:'100px'}}>&nbsp;</div>

        </div>
        

    )
}

export default DesktopMiddle1